a.btn-stripe:before, button.btn-stripe:before {
  content: "";
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  background-image: repeating-linear-gradient(-45deg, #000, #000 1px, #0000 2px 5px);
  background-size: 7px 7px;
  border: 2px solid #000;
  border-top-width: 1px;
  border-radius: 0 0 .5rem .5rem;
  width: 100%;
  height: 14px;
  transition: all .3s;
  display: block;
  position: absolute;
  bottom: -8px;
  left: 0;
}

a.btn-stripe:hover, button.btn-stripe:hover {
  transform: translate(0, 3px);
}

a.btn-stripe:hover:before, button.btn-stripe:hover:before {
  bottom: -5px;
}

a.btn-stripe:active, button.btn-stripe:active {
  transform: translate(0, 7px);
}

